rgba(83, 162, 190, 1);
rgba(29, 132, 181, 1);
rgba(19, 46, 50, 1);
rgba(23, 96, 135, 1);
*/

html, body, #root {
  height: 100%;
}

body {
  background-color: rgba(10, 34, 57, 1);
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.App {
  display: -webkit-flex;
  display: flex;
  height: 100%;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.main-title {
  color: rgb(255, 255, 255);
  margin: 24px;
}

.Button {
  color: white;
  margin: 24px;
  font-size: 2em;
  padding-left: 16px;
  padding-right: 16px;
  border: 0px solid transparent;
}

.Button.start {
  background: rgba(29, 132, 181, 1);
}

.Button.pause {
  background: rgba(23, 96, 135, 1);
}

.Button.clear {
  background: red;
}

.timer-info {
  color: white;
  font-size: 5em;
  margin: 24px;
}

